<template>
  <div class="sp">
    <tabbar></tabbar>

    <div v-for="item in sp" :key="item.id">
      <div class="box">
        <!-- 大图片 -->
        <div class="big_img">
          <div  class="big_img2">
            <div>
              <img style="width: 350px; height: 350px;" :src="src2" />
            </div>
            <!-- 小图片 -->
            <div class="box_bottom">
              <div
                class="x_tp"
                v-for="item2 in sp[0].imgarr"
                :key="item2.id"
              >
                <img style="cursor: pointer;" @click="chang_img(item2.id)" :src="item2.src" alt="" />
              </div>
            </div>
          </div>

          <div>
            <div class="wz_title">{{ item.title }}</div>
            <div style="border: 1px solid black"></div>
            <div class="wz">
              <span style="color: #828282">价格：</span
              ><span style="font-szie: 30px; color: #2b2b2b; font-weight: 900">
                ￥{{ item.price }}</span
              >
            </div>
            <div class="wz">
              <span style="color: #828282">编号：</span>{{ item.number }}
            </div>
            <div class="wz">
              <span style="color: #828282">类型：</span>{{ item.type }}
            </div>
            <div class="wz">
              <span style="color: #828282">规格：</span>{{ item.norm }}
            </div>
            <div class="wz">
              <span style="color: #828282">品牌：</span>{{ item.pp }}
            </div>
            <div class="wz" style="color:black;margin-top:20px;">
              想要预约或者购买茶业可以点击上方导航栏的微信客服，扫描二维码添加微信客服
            </div>
          </div>
        </div>
      </div>
      <div
        class="btn"
        v-for="item in sp"
        :key="item.id + 'a'"
      >
        <div>
          <el-button @click="last(2)">上一页</el-button
          >
        </div>
        <div>
          <el-button @click="next(4)">下一页</el-button>
        </div>
      </div>
      <!-- 分割线 -->
      <div class="fgx">
        <el-divider><span style="font-size: 30px">商品详情</span></el-divider>
      </div>

      <!-- 详情 -->
      <div class="b">
        <el-image
          style="width: 600px"
          :src="item.b_img"
          :preview-src-list="item.srcList"
        >
        </el-image>
      </div>
    </div>
  </div>
</template>

<script>
import Tabbar from "../../PC/Tabbar.vue";
export default {
  components: { Tabbar },
  name: "Sp",
  data() {
    return {
      id: "",
      src2: "",
      sp: [
        {
          id: 1,
          title: "霸道班章",
          price: "3333.00",
          number: "L2",
          type: "普洱茶",
          norm: "357g",
          pp:'同记茶业',
          b_img: 'https://image.baidu.com/search/down?url=https://tva1.sinaimg.cn/large/006D2oSpgy1h18028hj0oj30af6bkqap.jpg',
          srcList: ['https://image.baidu.com/search/down?url=https://tva1.sinaimg.cn/large/006D2oSpgy1h18028hj0oj30af6bkqap.jpg'],
          imgarr: [
            {
              id: 1,
              src: 'https://image.baidu.com/search/down?url=https://tvax1.sinaimg.cn/large/006D2oSpgy1h18035qls6j301o01oglf.jpg',
              src2: 'https://image.baidu.com/search/down?url=https://tva3.sinaimg.cn/large/006D2oSpgy1h1802jqbfdj30b40b4q54.jpg',
            },
            {
              id: 2,
              src: 'https://image.baidu.com/search/down?url=https://tvax3.sinaimg.cn/large/006D2oSpgy1h18035v1t4j301o01o743.jpg',
              src2: 'https://image.baidu.com/search/down?url=https://tvax2.sinaimg.cn/large/006D2oSpgy1h1802jvi6gj30m80m8wnl.jpg',
            },
            {
              id: 3,
              src: 'https://image.baidu.com/search/down?url=https://tva1.sinaimg.cn/large/006D2oSpgy1h18035zw8qj301o01oweb.jpg',
              src2: 'https://image.baidu.com/search/down?url=https://tvax2.sinaimg.cn/large/006D2oSpgy1h1802kobqnj30m80m8jxr.jpg',
            },
            {
              id: 4,
              src: 'https://image.baidu.com/search/down?url=https://tvax1.sinaimg.cn/large/006D2oSpgy1h180366z3oj301o01odfn.jpg',
              src2: 'https://image.baidu.com/search/down?url=https://tvax4.sinaimg.cn/large/006D2oSpgy1h1802jl5p6j30m80m8dk2.jpg',
            },
          ],
        },
      ],
    };
  },
  methods: {
    get_id(id) {
      this.id = this.$route.query.id;
      document.title = this.sp[0].title;
      this.src2 = this.sp[0].imgarr[0].src2;
    },
    chang_img(img_id) {
        this.src2 = this.sp[0].imgarr[img_id - 1].src2;
    },
    last(id) {
      this.$router.push("/sp_"+id+"_p?id=" + id);
      this.get_id();
    },
    next(id) {
      this.$router.push("/sp_"+id+"_p?id=" + id);
      this.get_id();
    },
  },
  created() {
    this.get_id();
  },
  mounted() {},
};
</script>

<style lang="scss" scope>
.sp {
  .box_bottom {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 350px;
    border: 1px solid black;
  }
  .box {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }
  .big_img {
    display: flex;
    justify-content: space-around;
  }
  .big_img2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 400px;
  }
  .big_img2 > div {
    border: 1px solid black;
  }
  .b {
    display: flex;
    justify-content: center;
  }
  .wz_title {
    font-size: 20px;
  }
  .wz {
    color: #888;
    font-size: 20px;
  }
  .fgx {
    min-width: 900px;
    margin: 0 4.7rem 0.5rem 4.7rem;
  }
  .btn {
    display: flex;
    justify-content: center;
  }
}
</style>